@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 20px;
}

body {
  min-height: 100vh;
  line-height: 1.6em;
  position: relative;
  font-family: 'Montserrat', sans-serif;
}

body::-webkit-scrollbar {
  width: 8px;
}

body::-webkit-scrollbar-track {
  box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
  background-color: #edf1f8;
}

body::-webkit-scrollbar-thumb {
  background-color: darkgrey;
  outline: 1px solid slategrey;
}

.my-body {
  padding-bottom: 50px;
  min-height: 100vh;
}

.App {
  padding-top: 20px;
  overflow-x: hidden;
}

.transparent-background-grey {
  background-color: #edf1f8;
  background-image: url(/portfoliolook/static/media/black-linen.8a1abc77.png);
  position: absolute;
  height: calc(100% - 180px);
  width: 100%;
  top: 180px !important;
}

.transparent-background-blue {
  background-color: #0300b1;
  background-image: url(/portfoliolook/static/media/black-linen.8a1abc77.png);
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 180px;
}

hr {
  margin-bottom: 30px;
  border: none;
  border-top: 3px #020065 solid;
}

.flex {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}

.flex>* {
  margin-right: 15px;
}

a {
  text-decoration: none;
  color: inherit;
}

.projects li {
  list-style: none !important;
}

small {
  font-size: 0.8rem;
}

.header {
  width: 90%;
  max-width: 1400px;
  margin: auto;
  position: relative;
  z-index: 1;
  display: grid;
  grid-template: "im free" "im nav";
  -webkit-justify-content: space-between;
          justify-content: space-between;
  margin-bottom: 20px;
}

.header img {
  grid-area: im;
  width: 250px;
  height: 250px;
  object-fit: cover;
  object-position: top;
  border-radius: 50%;
  border: #e45926c4 solid 10px;
  transition: border 300ms ease-in-out;
  box-shadow: 10px 64px 66px 6px rgba(0,0,0,0.1);
  box-shadow: 5px 5px 15px 5px rgba(0,0,0,0.23);
  -webkit-animation: showImage 500ms ease-in-out;
          animation: showImage 500ms ease-in-out;
}

.header img:hover {
  border: #e45926 solid 10px;
}

.header ul {
  grid-area: nav;
  align-self: start;
  font-size: 1.4rem;
  font-weight: 700;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
  margin-bottom: 10px;
  color: white;
  position: relative;
  bottom: 5px;
  -webkit-animation: showNav 300ms ease-in-out 500ms forwards;
          animation: showNav 300ms ease-in-out 500ms forwards;
  -webkit-transform: translateY(-20%);
          transform: translateY(-20%);
  opacity: 0;
}

.header ul li {
  box-shadow: 5px 5px 15px 5px rgba(0,0,0,0.23);
  list-style: none;
  margin-left: 1rem;
  padding: 10px 15px;
  background: #e45826;
  border-radius: 30px;
  cursor: pointer;
  transition: background 200ms ease-in-out;
}

.header ul li:hover {
  background: #e7693d;
}

.header ul li.current {
  background: #020065;
}

.header ul li:focus {
  outline: none;
}

@-webkit-keyframes showNav {
  100% {
    -webkit-transform: none;
            transform: none;
    opacity: 1;
  }
}

@keyframes showNav {
  100% {
    -webkit-transform: none;
            transform: none;
    opacity: 1;
  }
}

@-webkit-keyframes showImage {
  0% {
    opacity: 0;
  }
}

@keyframes showImage {
  0% {
    opacity: 0;
  }
}

@media (max-width: 960px) {
  .header {
    display: block;
    text-align: center;
  }

  .header img {
    margin-bottom: 20px;
  }

  .header ul {
    -webkit-justify-content: space-around;
            justify-content: space-around;
  }
}

@media (max-width: 650px) {
  html {
    font-size: 15px;
  }

  .header ul {
    font-size: 1rem;
    -webkit-justify-content: space-between;
            justify-content: space-between;
  }

  .header ul li {
    margin-left: 0;
  }
}

@media (max-width: 350px) {
  .header ul {
    font-size: 0.9rem;
  }

  .header ul li {
    padding: 10px;
  }
}

.main {
  position: relative;
  border: 5px solid #02007e;
  width: 90%;
  max-width: 1400px;
  margin: auto;
  z-index: 2;
  background-color: #5785bd;
  border-radius: 5px;
  padding: 0 1rem;
  line-height: 1.8em;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  overflow-y: hidden;
  box-shadow: 5px 5px 15px 5px rgba(0,0,0,0.23);
  margin-bottom: 30px;
  background-image: url(data:image/png;base64,);
}

.main>div {
  width: 100%;
  padding: 40px 70px;
}

.main::-webkit-scrollbar {
  width: 8px;
}

.main::-webkit-scrollbar-track {
  box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
}

.main li {
  list-style-type: circle;
}

.main::-webkit-scrollbar-thumb {
  background-color: darkgrey;
  outline: 1px solid slategrey;
}

.main>div>div {
  color: black;
}

.main .load {
  position: absolute;
  top: 47%;
  left: 50%;
}

@media (max-width: 700px) {
  .main {
    padding: 0 0.5rem;
  }

  .main>div {
    padding: 40px 1.5rem;
  }
}

.aboutme {
  height: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: flex-start;
          align-items: flex-start;
}

.aboutme ul {
  margin-bottom: 20px;
  width: 100%;
}

.aboutme .download {
  background: #e45826;
  text-decoration: none;
  color: white;
  padding: 8px 15px;
  transition: background 300ms ease-in-out;
}

.aboutme .download:hover {
  background: #e7693d;
  -webkit-animation: none;
          animation: none;
  -webkit-transform: translateY(-0.5px) !important;
          transform: translateY(-0.5px) !important;
}

.aboutme .icon {
  font-size: 35px;
  color: #4a4e58;
  transition: color 300ms ease-in-out,-webkit-transform 300ms ease-in-out;
  transition: color 300ms ease-in-out,transform 300ms ease-in-out;
  transition: color 300ms ease-in-out,transform 300ms ease-in-out,-webkit-transform 300ms ease-in-out;
}

.aboutme .icon:hover {
  color: #33353c;
  -webkit-transform: rotate(10deg);
          transform: rotate(10deg);
}

.aboutme hr {
  margin: 15px 0;
}

.aboutme .flexed {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
}

.aboutme .flexed li {
  margin-left: 20px;
  margin-right: 30px;
}

.aboutme .flexed img {
  border-radius: 50%;
  object-fit: cover;
  width: 100px;
  height: 100px;
  border: 10px solid transparent;
  transition: border-color 300ms ease-in-out;
}

.aboutme .flexed img:hover {
  border-color: rgba(246,131,0,0.517);
}

@media (max-width: 700px) {
  .aboutme {
    display: block;
  }
}

.Skills .skills {
  display: grid;
  grid-template: "basic data data"  "web web web" auto/ 270px 1fr 1fr;
  position: relative;
  grid-row-gap: 20px;
  row-gap: 20px;
}

.Skills .skills div:nth-of-type(1) {
  grid-area: basic;
  border-right: 3px #020065 solid;
  border-bottom: 3px #020065 solid;
}

.Skills .skills div:nth-of-type(2) {
  grid-area: data;
  border-bottom: 3px #020065 solid;
  padding-bottom: 20px;
  padding-left: 20px;
}

.Skills .skills div:nth-of-type(3) {
  grid-area: web;
  margin-bottom: 20px;
}

.Skills .skills div ul {
  display: grid !important;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
}

.Skills .skills div ul li {
  list-style: none;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}

.Skills .skills div ul li:hover img {
  -webkit-animation: moving 300ms ease-in-out;
          animation: moving 300ms ease-in-out;
}

.Skills .skills div ul li img {
  width: 27px;
  height: 27px;
}

@-webkit-keyframes moving {
  50% {
    -webkit-transform: translateY(-5px);
            transform: translateY(-5px);
  }

  100% {
    -webkit-transform: none;
            transform: none;
  }
}

@keyframes moving {
  50% {
    -webkit-transform: translateY(-5px);
            transform: translateY(-5px);
  }

  100% {
    -webkit-transform: none;
            transform: none;
  }
}

@media (max-width: 600px) {
  .Skills .skills {
    display: block;
  }

  .Skills .skills div:nth-of-type(1) {
    border-right: none;
  }

  .Skills .skills div:nth-of-type(2) {
    padding-left: 0;
  }

  .Skills .line {
    display: none;
  }
}

.form form {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: stretch;
          align-items: stretch;
  margin-top: 10px;
}

.form form input,
.form form textarea {
  font-family: "Montserrat",sans-serif;
  border: 3px solid #02007e;
  border-radius: 5px;
  margin-top: 15px;
  height: 40px;
  font-size: 1.2rem;
  transition: border 200ms ease-in-out;
}

.form form input:focus,
.form form textarea:focus {
  outline: none;
  border: 5px solid #0300b1;
}

.form form input:hover,
.form form textarea:hover {
  border: 5px solid #02007e;
}

.form form input[name=description],
.form form textarea[name=description] {
  height: 200px;
  margin-bottom: 20px;
  resize: none;
}

.form form button {
  padding: 15px 0;
  color: white;
  background: #e45826;
  border: none;
  border-radius: 5px;
  font-size: 1.2rem;
}

.form form button:hover {
  background: #e7693d;
}

.images {
  width: 100%;
  margin: auto;
  margin-bottom: 50px;
  height: 70vh;
  border: #02007e solid 5px;
  position: relative;
  overflow: hidden;
}

.images .theImage {
  position: absolute;
  width: 100%;
  height: 100%;
  background-position: top !important;
  background-size: cover !important;
}


